<template>
  <div class="skill_box">
     <div class="skill_card_cont">
          <div class="skill_card skill_item" v-for="skill in skills">
              <div class="skill_ad" :class="skill.style">
                <span>{{skill.name1}}</span>
              </div>
              <div class="skill_desc">
                  <h2 class="skill_name">{{skill.name2}}</h2>
                  <p class="skill_txt">{{skill.txt}}</p>
                  <div class="skill_age">
                      <h3>{{skill.power}}</h3>
                      <p>{{skill.power_txt}}</p>
                   </div>
                  <p class="tip">左右滑动查看更多</p>
              </div>
          </div>
          <div class="skill_item">
              <img class="skill_bg" src="static/images/skill_bg.png">
          </div>
     </div>
  </div>
</template>

<script>

  export default{
      name:'skill',
      data: function () {
              return {
                  skills:[]
              }
          },
      created: function () {
          this.$http({url: 'static/json/skills.json', method: 'GET'}).then(function (response) {
              this.skills=response.data;
          });
          var disableScroll = function(){
              $(document.body).on('mousewheel', preventDefault).on('touchmove', preventDefault);
          };
          // 外部禁用
          disableScroll();
      }
  }


</script>






