<template>
  <div class="home" name="home">
       <div class="mine_info mine_box">
            <div class="fl mine_pic">
              <img src="../images/mine.png">
            </div>
            <div class="mine_text">
              <h3 class="main_txt">陈闯<small class="aff_txt ml_10">男</small></h3>
              <p class="mine-addr aff_txt"><i class="iconfont icon-dingwei"></i>常州.新北<span class="ml_10" id="distance"></span></p>
              <p class="mine-weather aff_txt">没有什么比投入更让人期待...</p>
            </div>
       </div>
       <div class="info_list mine_box">
            <router-link v-for="info in infos " class="info_item" :href="info.href" :to="info.link"><i class="iconfont" :class="info.icon"></i>{{info.name}}<div class="item_aff"><span>{{info.text}}</span><i v-if="info.more" class="iconfont icon-right"></i></div> </router-link>
       </div>
    </div>
</template>

<script>
      export default{
          name: 'home',
          data: function () {
              return {
                  infos:[]
              }
          },
          created: function () {
              this.$http.get('static/json/me.json').then(response => {
                  this.infos=response.data;
                }, response => {
                  console.log(response.data);
                });

              var lng, lat;
              setWeather();
              setInterval(setWeather,600000);
          }
      }
</script>




