<template>
    <div class="works">
        <div class="works_menu">
          <div class="menu_box box_flex">
              <a v-for="(nav,index) in navs" :class="[{flex:true},{active:nav.active}]" @click="getWork(nav.get),makeActive(index)">{{nav.name}}</a>
          </div>
        </div>
        <div class="works_box">
          <a class="work_item" :href="work.link" v-for="work in works">
            <img :src="work.pic">
            <p>{{work.name}}</p>
          </a>
        </div>
    </div>
</template>

<script>
  export default{
      name:'works',
      data:function(){
          return{
              works:[],
              navs:[
                  {name:'2018~2017',get:"first.json",active:true},
                  {name:'2016~2015',get:"second.json",active:false},
                  {name:'2015~2012',get:"third.json",active:false}
              ]
          }
      },
      methods:{
          getWork:function(url){
              this.$http({url: 'static/json/'+url, method: 'GET'}).then(function (response) {
                  this.works=response.data;
              });
          },
          makeActive: function(index){
              for(var i=0; i<this.navs.length;i++){
                  this.navs[i].active = false;
                  console.log[i]
              }
              this.navs[index].active = true;
          }
      },
      created: function () {
          this.$http({url: 'static/json/first.json', method: 'GET'}).then(function (response) {
              this.works=response.data;
          });
      }
  }
</script>





